.modal-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: none;
  background-color: #ffffff; }
  .modal-menu--show {
    display: block;
    width: 100%;
    height: 100%; }

.modal-menu__list {
  margin-top: 100px;
  margin-bottom: 134px;
  padding: 0;
  list-style-type: none; }

.modal-menu__item {
  position: relative;
  margin-bottom: 30px;
  min-height: 60px;
  color: #48494d;
  background-color: #f6f6f6;
  font-weight: 400;
  font-size: 20px;
  line-height: 28px; }
  .modal-menu__item a {
    display: block;
    padding-top: 16px;
    padding-left: 17px;
    padding-bottom: 16px; }
  .modal-menu__item:hover {
    color: #0ad9c6;
    cursor: pointer; }
  .modal-menu__item::before, .modal-menu__item::after {
    content: "";
    position: absolute;
    right: 20px;
    width: 15px;
    height: 4px;
    border-radius: 2px;
    background-color: #91929b; }
  .modal-menu__item::before {
    top: 22px;
    transform: rotate(45deg); }
  .modal-menu__item::after {
    top: 32px;
    transform: rotate(-45deg); }
  .modal-menu__item:hover::before, .modal-menu__item:hover::after {
    background-color: #0ad9c6; }

.modal-menu__close {
  position: absolute;
  top: 0;
  right: 28px;
  width: 50px;
  height: 52px;
  background-color: transparent;
  border: 0;
  cursor: pointer; }
  .modal-menu__close::before, .modal-menu__close::after {
    content: "";
    position: absolute;
    top: 25px;
    left: 6px;
    width: 40px;
    height: 2px;
    border-radius: 2px;
    background-color: #d0d0d1; }
  .modal-menu__close::before {
    transform: rotate(45deg); }
  .modal-menu__close::after {
    transform: rotate(-45deg); }
  .modal-menu__close:hover::after {
    transform: scale(1.1) rotate(-45deg);
    background-color: #0ad9c6; }
  .modal-menu__close:hover::before {
    transform: scale(1.1) rotate(45deg);
    background-color: #0ad9c6; }
